import find from 'lodash/find'
import { useCallback, useEffect, useMemo, useState } from 'react'

import * as api from './api'
import LayoutGenerationError from '../../Dialogs/LayoutGenerationError'
import { Select } from '../Controls'
import LocalActions from './LocalActions'

function LocalPicker ({ onSelect, onError }) {
  const [keyboards, setKeyboards] = useState([])
  const [keyboard, setKeyboard] = useState(null)

  const choices = useMemo(() => (
    keyboards.map(name => ({ id: name, name }))
  ), [keyboards])

  useEffect(() => {
    (async function () {
      const keyboards = await api.loadAvailableKeyboards()
      const persistedKeyboard = JSON.parse(localStorage.getItem('localSourceSelectedKeyboard') || 'null')
      const initialSelection = keyboards.includes(persistedKeyboard)
        ? persistedKeyboard
        : keyboards[0]

      setKeyboards(keyboards)
      setKeyboard(initialSelection)
    })()
  }, [setKeyboards, setKeyboard])

  useEffect(() => {
    (async function () {
      if (keyboard) {
        try {
          const { info, keymap, sensors } = await api.loadKeyboardFiles(keyboard)
          const { layouts } = info
          const preferredLayout = keymap.chosen['zmk,matrix_transform']
            ? keymap.chosen['zmk,matrix_transform'].replace(/^&/, '')
            : null

          const defaultLayout = Object.keys(layouts)[0]
          const layoutMetadata = (
            layouts[preferredLayout] ||
            layouts[defaultLayout]
          )

          onSelect({
            source: 'local',
            keyboard,
            layoutMetadata,
            layout: layoutMetadata.layout,
            keymap,
            sensors
          })
        } catch (err) {
          if (err.response) {
            const error = await err.response.json()
            if (error.name === 'LayoutGenerationError') {
              onError({ dialog: LayoutGenerationError })
              return
            }
          }

          throw err
        }
      }
    })()
  }, [onSelect, onError, keyboard])

  const handleSelectKeyboard = useCallback(({ id: keyboard }) => {
    localStorage.setItem('localSourceSelectedKeyboard', JSON.stringify(keyboard))
    setKeyboard(keyboard)
  }, [setKeyboard])

  const selection = find(choices, { id: keyboard })

  return (
    <>
      <Select
        label="Keyboard"
        value={selection}
        options={choices}
        onChange={handleSelectKeyboard}
        getOptionValue={option => option.id}
        getOptionLabel={option => option.name}
      />
      <LocalActions />
    </>
  )
}

export default LocalPicker
