import isEqual from 'lodash/isEqual'
import { useDispatch, useSelector } from 'react-redux'

import * as api from './api'
import ActionButton from '../../Common/ActionButton'

const stateSelector = state => state

export default function LocalActions () {
  const dispatch = useDispatch()
  const state = useSelector(stateSelector)
  const {
    sourceOther,
    keyboard,
    _unmodifiedKeymap
  } = state

  const hasChanged = keyboard && !isEqual(keyboard.keymap, _unmodifiedKeymap)

  async function handleCompile () {
    const keyboard = sourceOther.keyboard
    const { keymap } = state.keyboard

    try {
      const updated = await api.updateKeymap(keyboard, keymap)
      dispatch({
        type: 'KEYMAP_SAVE_SUCCESS',
        keymap: updated
      })
    } catch {
      dispatch({ type: 'KEYMAP_SAVE_FAILURE' })
    }
  }

  return (
    <ActionButton disabled={!hasChanged} onClick={handleCompile}>
      Save Changes
    </ActionButton>
  )
}
